$('.J-btn-yz').click(function(){
    var v=$('.user-name').find('input').val();
    if(!v){
        alert('用户名不能为空')
        $('#file').show();
    }
    else{
     $('#file').show();
    }
});
// ========== 判断用户名是否为空 ==========


$('#file').click(function(){
    setTimeout(function(){
        // var userName = $('.user-name').find('input').val();
        // var userTit = $('.user-tit').find('input').val();
        // var userTxar = $('.user-txar').find('textarea').val().replace(/^ +| +$/g,'');
        // var userTxar = $('.user-txar').find('.txar').html();
        // userTxar2=userTxar.replace(/\n|\r\n/gm,"<br />");
        $('.J-hide').hide();
        $('.infobg').css('margin',"0 .31rem");
        $('.text-top').hide();
        $('.clip-bot').show();
        // $('.iptbox > *').hide();
        // $('.iptbox > .txt-var').show();
        $('.btn-up').hide();
        // $('.name-val').show().text(userName);
        // $('.tit-val').show().text(userTit);
        // $('.txar-val').show().html(userTxar);

        $('.iptbox').addClass('iptbox_no');
        $('.qr-code').show();

        var userTit = $('.user-tit').find('.ipt').val();
        if(!userTit){
            $('.user-tit').find('.ipt').hide();
        }

        var userTxar = $('.user-txar').find('.txar').val();
        if(!userTxar){
            $('.user-txar').find('.txar').hide();
        }
    },3000)
});
// ========== 上传照片隐藏其他内容 ==========


$(".clipBtn").click(function(){
    $(".view").css({
        "display": "block"
    });
    $('.J-hide').show();
    $('.clip-bot').hide();
    $('.infobg').css('margin',"0");
    $('.J-btn-up').hide();
    $('.J-btnSave').css('display','inline-block');
    $('.J-btns').css('display','none')
});
// ========== 点击确定按钮，裁剪图片层显示 ==========


$('.J-c1').click(function(){
	$(this).addClass('hide');
	$('.J-c2').removeClass('hide');
	$('.iptbox').removeClass('ipt-c1').addClass('ipt-c2');
})
$('.J-c2').click(function(){
	$(this).addClass('hide');
	$('.J-c3').removeClass('hide');
    $('.iptbox').removeClass('ipt-c2').addClass('ipt-c3');
})
$('.J-c3').click(function(){
	$(this).addClass('hide');
	$('.J-c1').removeClass('hide');
	$('.iptbox').removeClass('ipt-c3').addClass('ipt-c1');
})
// ========== 切换颜色 ==========


$('.J-secbtn').click(function(){
    $('.layer-wrap').show();
});
$('.J-cloes').click(function(){
    $('.layer-wrap').hide();
});
$('.J-radio').click(function(){
    $('.r-icn').removeClass('active');
    $(this).find('.r-icn').addClass('active');
    // $('.user-txar').find('.txar').html($(this).find('.flex1').html());
    // $('.user-txar').find('.txar').text($(this).find('.flex1').html());
    var tempHtml = '';
    var $ps = $(this).find('.flex1 p');
    for(var i=0;i<$ps.length;i++){
        tempHtml += $ps[i].innerText + '\n';
    }
    $('.user-txar').find('.txar').text(tempHtml);
});
